<template>
  <div class="row" v-if="isReady">
    <div class="col-12 mt-3 flex-x-between">
      <div class="flex-align-center">
        <h3 class="my-0">Products</h3>
      </div>

      <div class="flex-align-center flex-end">
        <el-button
          class="mr-3 my-0"
          @click="$router.push('/product-report')"
          type="primary"
          v-ripple.400
        >
          Report
        </el-button>
        <el-button
          class="mx-0 my-0"
          @click="$router.push('/add-new-product')"
          type="primary"
          v-ripple.400
        >
          Add product
        </el-button>
      </div>
    </div>
    <div class="col-12 list-table mt-3">
      <div class="box pt-3">
        <div class="row">
          <!-- <div class="col-12"><div class="b-divider mb-1 mt-0"></div></div> -->
          <div
            class="
              col-12
              mb-1
              d-flex
              justify-content-between
              align-items-center
            "
          >
            <div class="flex-align-center">
              <el-select
                v-model="filters.pageSize"
                class="mr-2 page-size-selector mr-2"
              >
                <el-option
                  v-for="page in 100"
                  :key="page"
                  :label="page * 15"
                  :value="page * 15"
                >
                </el-option>
              </el-select>
              <el-input
                v-model="inputHelper"
                @change="setFilterQuery"
                class="mr-2 table-search"
                placeholder="Type something"
                prefix-icon="bx bx-search"
              >
              </el-input>
            </div>

            <el-popover placement="bottom" width="200" trigger="click">
              <p class="font-weight-bold">Sort By</p>
              <div class="b-divider my-2"></div>
              <div class="w-100">
                <el-radio
                  v-model="filters.order_by"
                  v-for="(sort, n) in sortList"
                  :key="sort.name"
                  :class="n !== sortList.length - 1 && 'mb-3'"
                  :label="sort.value"
                >
                  {{ sort.name }}
                </el-radio>
              </div>
              <el-button
                v-ripple.400="'rgba(0,0,0,0.1)'"
                class="flat no-width"
                slot="reference"
              >
                <i class="bx bx-sort mr-1"></i> Sort
              </el-button>
            </el-popover>
          </div>
        </div>
        <div class="w-100 el-main-table" v-loading="loading">
          <div class="el-table-header" v-if="selected.length > 0">
            <div class="d-flex align-items-center">
              <el-checkbox
                :indeterminate="selected.length == products.results.length"
                v-model="allCheck"
                @change="$refs.multipleTable.toggleAllSelection"
                >{{ selected.length }} selected</el-checkbox
              >
              <el-dropdown @command="updateStatus" trigger="click">
                <p
                  class="el-dropdown-link ml-3 pointer primary hover-underline"
                >
                  Actions
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </p>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item
                    v-for="action in actions"
                    :key="action"
                    :command="action"
                  >
                    {{ action }}</el-dropdown-item
                  >
                </el-dropdown-menu>
              </el-dropdown>
            </div>
          </div>
          <el-table
            ref="multipleTable"
            :data="products.results"
            style="width: 100%"
            @row-click="
              (row) =>
                $router.push({
                  name: 'Edit product',
                  params: { id: row.id },
                })
            "
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection"> </el-table-column>

            <el-table-column label="Name" width="250">
              <template slot-scope="scope">
                <img
                  :src="scope.row.image"
                  style="
                    height: 80px;
                    width: 80px;
                    object-fit: cover;
                    border-radius: 5px;
                    margin-right: 5px;
                  "
                  alt=""
                />
              </template>
            </el-table-column>
            <el-table-column label="Price" width="150">
              <template slot-scope="scope">
                {{ scope.row.price + "MMK" }}
              </template>
            </el-table-column>
            <el-table-column label="Barcode">
              <template slot-scope="scope">
                <span>{{ scope.row.barcode }}</span>
              </template>
            </el-table-column>
            <el-table-column label="Stocks">
              <template slot-scope="scope">
                <span>{{ scope.row.number_of_stock }}</span>
              </template>
            </el-table-column>
            <template #empty>
              <el-empty description="No data found!">
                <template #image> <empty /> </template
              ></el-empty>
            </template>
          </el-table>
          <div class="el-table-footer" v-if="products.total_pages !== 1">
            <el-pagination
              background
              @current-change="
                (index) => ((filters.page = index), getproducts())
              "
              :current-page="parseInt(filters.page)"
              layout="prev, pager, next"
              :page-count="products.total_pages"
            >
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { empty } from "../../common";
export default {
  data() {
    return {
      inputHelper: "",
      filters: {
        query: "",
        order_by: "-updated_at",
        pageSize: 10,
        page: 1,
      },
      loading: false,
      actions: ["Delete"],
      sortList: [
        {
          name: "Created (oldest first)",
          value: "created_at",
        },
        {
          name: "Created (newest first)",
          value: "-created_at",
        },
        {
          name: "Updated (oldest first)",
          value: "updated_at",
        },
        {
          name: "Updated (newest first)",
          value: "-updated_at",
        },
        {
          name: "Customer A–Z",
          value: "customer__name",
        },
        {
          name: "Customer Z–A",
          value: "-customer__name",
        },
      ],
      loadingDelivey: false,
      deliveries: [],
      selectedDate: null,
      selectedDelivery: null,
      allCheck: false,
      gettingOrder: false,
      page: 1,
      max: 5,
      selected: [],
      isReadyToGet: false,
      products: {
        current_page: 1,
        results: [],
        total_pages: 0,
      },
    };
  },
  components: { empty },
  computed: {
    ...mapState(["isReady", "store"]),
  },
  created() {
    this.$store.commit("setIsReady", false);
  },
  mounted() {
    this.filters = {
      query: this.$route.query.query || "",
      order_by: this.$route.query.order_by || "-updated_at",
      pageSize: this.$route.query.page_size || "10",
      page: this.$route.query.page || "1",
    };
    (this.inputHelper = this.$route.query.query || ""),
      (this.isReadyToGet = true);
    this.getproducts();
  },
  watch: {
    filters: {
      handler: function (value) {
        if (this.isReadyToGet) {
          this.$router.push({
            path: "/products",
            query: {
              query: value.query,
              page_size: value.pageSize,
              page: value.page,
              order_by: value.order_by,
            },
          });
        }
      },
      deep: true,
    },
  },
  methods: {
    updateStatus(com) {
      if (com == "Delete") {
        this.deleteProducts();
      }
    },
    deleteProducts() {
      this.loading = true;
      var data = [];
      this.selected.forEach((ele) => {
        data.push(ele.id);
      });
      var url = `products/delete/`;
      this.$axios
        .post(url, { data: data })
        .then(() => {
          this.getproducts();
        })
        .catch((err) => {
          this.loading = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    setFilterQuery() {
      this.filters.query = this.inputHelper;
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.selected = val;
    },

    getproducts() {
      this.loading = true;
      this.gettingOrder = true;
      if (this.filters.date === null) {
        this.filters.date = "";
      }
      var url = `products/?page=${this.filters.page}&page_size=${this.filters.pageSize}&query=${this.filters.query}&order_by=${this.filters.order_by}`;
      this.$axios
        .get(url)
        .then((response) => {
          this.loading = false;
          this.gettingOrder = false;
          this.products.results = response.data.results;
          this.products.total_pages = response.data.total_pages;
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.loading = false;
          this.gettingOrder = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
  },
};
</script>

